// librarie - select2 - reset
.select2-dropdown {
	background: transparent;
	padding: 0 $field_padding;
	border: 0;
	.select2-search {
		background: $white;
		padding: 0;
		border-left: 1px solid $lightGray;
		border-right: 1px solid $lightGray;
		border-top: 1px solid $lightGray;
		border-bottom: 0;
		position: relative;
		input {
			@include transition();
			@include labels($tc: $text);
			font-weight: $fwLight;
			border: 0 !important;
			height: $field_height;
			line-height: $field_height;
			padding: 0 $field_padding 0 50px;
			background: $white;
			&:focus {
				background: $white3;
			}
		}
		&:after {
			content: '';
			display: block;
			width: 20px;
			height: 20px;
			position: absolute;
			top: 12px;
			left: 20px;
			z-index: 2;
			background: url(#{$media_icons}icon_search.svg);
		}
	}
	.select2-results {
		@include rounded(bottom, left, $defaultRadius);
		@include rounded(bottom, right, $defaultRadius);
		background: $white;
		border: 1px solid $lightGray;
		.select2-results__options {
			padding-top: 5px;
			@include scroll();
			.select2-results__option {
				@include paragrafos($mb: 0);
				padding: 8px $field_padding;
				background: $white;
			}
			.select2-results__option--selectable {
				//background: $field_bg;
			}
			.select2-results__option--highlighted {
				background: $primary;
				color: $white;
			}
			.select2-results__option--selected {
				background: $field_bg_focus;
				color: $primaryDark;
			}
		}
	}
}
.select2-selection__rendered {
	.select2-selection__choice {
		border-radius: 12px !important;
		height: 24px !important;
		overflow: hidden !important;
		border: 0 !important;
		margin-left: 0 !important;
		margin-top: 0 !important;
		margin-bottom: 5px !important;
		margin-right: 5px !important;
		background: $primary !important;
	}
	.select2-selection__choice__remove {
		@include flexWidth(24px);
		@include roundedAll(12px);
		height: 24px !important;
		line-height: 24px !important;
		padding: 0 !important;
		border: 0 !important;
		text-align: center !important;
		color: $white !important;
		font-size: 16px !important;
		font-weight: $fwMedium !important;
		background: $primaryDark !important;
	}
	.select2-selection__choice__display {
		@include labels($tc: $white, $fs: 14px);
		font-weight: $fwRegular !important;
		padding: 0 12px 0 6px !important;
		pointer-events: none !important;
	}
}

// librarie - swiper
.swiper-pagination {
	position: relative;
	bottom: 0 !important;
	display: flex;
	flex-direction: row;
	align-items: flex-start;
	justify-content: flex-start;
	.swiper-pagination-bullet {
		@include opacity(1);
		@include roundedAll(0);
		display: block;
		width: 7px;
		height: 7px;
		background: $secondary;
		margin-right: 8px !important;
		margin-left: 0 !important;
		&.swiper-pagination-bullet-active {
			background: $secondaryDark;
		}
	}
}
.swiper-button-next,
.swiper-button-prev {
	width: 20px;
	height: 30px;
	@include bg(assets, 'arrow_right_g.png');
	&.--white {
		@include bg(assets, 'arrow_white_r.png');
	}
}
.swiper-button-prev {
	@include rotate(180deg, center);
}

