}
.l-footer {
	background: $primaryLight;
	padding: 60px 0;
	p, span {
		color: $white;
		display: block;
		text-align: center;
	}
	p {
		text-transform: uppercase;
		font-size: 22px;
	}
	span {
		font-size: 15px;
	}
}
.l-footerMobile {
	position: fixed;
	bottom: 0;
	left: 0;
	z-index: 20;
	display: flex;
	flex-direction: row;
	align-items: center;
	justify-content: flex-start;
	width: 100%;
	height: 54px;
	background: $secondary;
	padding: 0 15px;
	&:hover {
		background: $secondaryDark;
	}
	&:after {
		position: absolute;
		top: 5px;
		right: 5px;
		display: block;
		width: 19px;
		height: 19px;
		content: '';
		@include bg(assets, 'bg_cta_caret.png', no-repeat, contain, inherit, top right);
	}
	span {
		text-transform: uppercase;
		line-height: 1;
		color: $primaryDark;
		font-size: 15px;
		font-weight: $fwMedium;
	}
}
.l-nav {
	@include transition();
	display: block;
	width: 100%;
	max-width: 1440px;
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	margin: 0 auto;
	z-index: 10;
	//background: $primary;
	padding: 60px 40px;
	&__menu {
		position: absolute;
		left: 15px;
		a {
			display: flex;
			flex-direction: row;
			align-items: center;
			justify-content: space-between;
			flex-wrap: nowrap;
			width: 72px;
			cursor: pointer;
			i {
				@include flexWidth(20px);
				* {
					display: block;
					width: 20px;
				}
			}
			span {
				@include transition();
				color: $white;
				text-transform: uppercase;
				font-size: 15px;
			}
			&:hover {
				span {
					color: $secondary;
				}
			}
		}
	}
	&__search {
		position: absolute;
		right: 25%;
		margin-right: -40px;
		width: 20px;
		height: 20px;
		display: flex;
		flex-direction: row;
		align-items: center;
		justify-content: center;
		a, img {
			display: block;
			width: 15px;
			height: 15px;
		}
	}
	&__cta {
		@include transition();
		position: absolute;
		top: -60px;
		right: -40px;
		display: block;
		a {
			@include transition();
			display: flex;
			flex-direction: row;
			align-items: flex-end;
			justify-content: flex-start;
			padding: 10px;
			width: 110px;
			height: 110px;
			@include bg(assets, 'bg_cta.png', no-repeat, cover, inherit, top right);
			line-height: 1;
			font-weight: $fwMedium;
			color: #282828;
			text-transform: uppercase;
			font-size: 14px;
			&:hover {
				@include opacity(0.8);
			}
		}
	}
	&__brand {
		@include flexWidth(200px);
		display: block;
		a, img {
			display: block;
			width: 100%;
		}
	}
	
	&.is-middle {
	
	}
	&.is-scrolled {
		background: $primary;
		padding-top: 30px;
		padding-bottom: 30px;
		.l-nav__cta {
			top: -30px;
		}
	}
	&.is-hidden {
		top: -300px;
	}
}
.l-navHidden {
	@include u-hideElement;
	@include transition();
	display: none;
	width: 100%;
	height: 100%;
	position: fixed;
	top: 0;
	left: 0;
	z-index: 11;
	
	&__mask {
		@include transition($duration: 150ms);
		display: block;
		width: 100%;
		height: 100%;
		position: fixed;
		top: 0;
		left: 0;
		z-index: 1;
		background: rgba(0,0,0,0.5);
	}
	&__content {
		@include transition($delay: 50ms, $duration: 280ms);
		@include scroll($bg: $white3, $bar: $lightGray);
		display: block;
		width: 100%;
		max-width: 420px;
		height: 100vh;
		background: $white;
		position: fixed;
		top: 0;
		z-index: 2;
		overflow: auto;
		padding: 30px 30px 90px 30px;
		&.--left {
			left: -500px;
		}
		&.--right {
			right: -500px;
		}
	}
	
	&.is-active {
		@include u-showElement;
		.l-navHidden__content {
			@include opacity(1);
			&.--left {
				left: 0;
			}
			&.--right {
				right: 0;
			}
		}
	}
	
	&__menu {
		display: block;
		> li {
			display: block;
			width: 100%;
			margin-bottom: 10px;
			&:last-of-type {
				margin-bottom: 0;
			}
			> a {
				color: $text;
				font-size: 16px;
			}
		}
	}
}
.l-headerHome {
	display: block;
	position: relative;
	overflow: hidden;
	&__content {
		display: block;
		position: relative;
		width: 100%;
		max-width: $containerWidth;
		margin: 0 auto;
	}
	.content__figure {
		position: relative;
		z-index: 1;
		display: block;
		width: 100%;
	}
	.content__call {
		position: absolute;
		z-index: 2;
		right: -130px;
		top: 60px;
		background: #151515;
		padding: 50px;
		h1 {
			color: $white;
			text-transform: uppercase;
			font-size: 52px;
			line-height: 1;
		}
		p {
			color: $white;
			font-size: 18px;
		}
		.c-linkArrow {
			position: absolute;
			right: 30px;
			bottom: -20px;
		}
	}
	.c-linkVideo {
		width: 100%;
		height: 540px;
	}
	
	&__effects {
		display: block;
		position: relative;
		z-index: 2;
		width: 100%;
		max-width: $containerWidth;
		margin: 0 auto;
		i {
			width: 105px;
			height: 100px;
			display: block;
			position: absolute;
			left: -125px;
			top: 0;
			img {
				display: block;
				width: 100%;
			}
		}
	}
	
	&__swiper {
		position: relative;
		&:before {
			content: '';
			display: block;
			height: 100%;
			width: calc((100% - 960px) / 2);
			position: absolute;
			top: 0;
			left: 0;
			z-index: 2;
			background: $primary;
		}
	}
	
	&__pagination {
		display: flex;
		flex-direction: row;
		align-items: flex-end;
		justify-content: flex-start;
		padding-left: 60px;
		position: absolute;
		bottom: 90px;
		left: 0;
		z-index: 3;
		&:after {
			content: '';
			display: block;
			width: 137px;
			height: 1px;
			background: $white;
			position: absolute;
			right: -148px;
			bottom: 3px;
		}
		.swiper-pagination-bullet {
			@include transition($duration: 350ms);
			@include roundedAll(0);
			@include opacity(1);
			color: rgba(255,255,255,0.2);
			background: transparent;
			text-transform: uppercase;
			font-size: 12px;
			margin-right: 15px;
			width: auto;
			height: auto;
			position: relative;
			top: 0;
			&.swiper-pagination-bullet-active {
				color: rgba(255,255,255,1);
				font-size: 22px;
				top: 1px;
			}
			&:last-of-type {
				margin-right: 0;
			}
		}
	}
}

// m
.m-defaultModal {
	@include u-hideElement;
	@include transition();
	position: fixed;
	top: 0;
	left: 0;
	z-index: 15;
	display: block;
	width: 100%;
	height: 100vh;
	background: $primaryDark;
	@include scroll();
	overflow: auto;
	max-height: 100vh;
	> .u-container {
		min-height: 100vh;
		background: $primary;
	}
	&__close {
		@include transition();
		position: absolute;
		right: 0;
		top: 0;
		display: flex;
		flex-direction: row;
		align-items: center;
		justify-content: center;
		width: 100px;
		height: 100px;
		background: $secondary;
		cursor: pointer;
		img {
			@include transition();
			display: block;
			width: 36px;
			height: 36px;
		}
		&:hover {
			background: $secondaryDark;
			img {
				@include rotate(15deg, center);
			}
		}
	}
	&__content {
		display: block;
		padding: 120px 40px;
	}
	
	&.is-active {
		@include u-showElement;
	}
	
	.u-spacer {
		display: block;
		width: 100%;
		max-width: 1100px;
		height: 1px;
		background: $primaryLight;
		position: relative;
		margin: 80px auto;
		&:before {
			content: '';
			display: block;
			width: 60px;
			height: 3px;
			position: absolute;
			top: -1px;
			left: 50%;
			margin-left: -30px;
			z-index: 2;
			background: $secondary;
		}
	}
}
