		width: 100%;
		max-width: 960px;
		padding-top: 80px;
		h3 {
			display: block;
			width: 100%;
			text-align: center;
			font-size: 60px;
			color: $white;
		}
		ul {
			margin-top: 60px;
			li {
				border-bottom: 1px solid $primaryLight;
				margin-bottom: 30px;
				padding-bottom: 30px;
				&:last-of-type {
