		max-width: 100%;
		&:hover {
			background: $primaryDark;
		}
	}
	.c-btn {
		font-size: 16px;
		font-family: $fontFamily__default;
	}
	
	.conviteSts {
		.c-btn {
			margin: 30px auto;
		}
		a.is-disabled {
			display: block;
			@include opacity(0.3);
			p {
