body.so-desktop .s-podcasts {
	&__swiper {
		.swiper-wrapper {
			@include calcWidthForPositionNegative(-20px);
			display: flex;
			flex-direction: row;
			align-items: flex-start;
			justify-content: space-between;
			flex-wrap: nowrap;
		}
		.swiper-slide {
			@include flexWidth(25%);
			padding: 0 20px;
		}
	}
}
.s-nextEvents {
	&__top {
		background: $primary;
		display: flex;
		flex-direction: row;
		align-items: center;
		justify-content: flex-end;
		padding-right: 110px;
	}
	.top__effect {
		display: block;
		width: 50px;
		img {
			display: block;
			width: 50px;
		}
	}
	.top__link {
		padding-top: 20px;
		margin-right: 20px;
		span {
			color: $white;
			text-transform: uppercase;
			font-size: 16px;
			padding-right: 10px;
		}
		strong {
			color: $secondary;
		}
		&:hover {
			span {
				text-decoration: underline;
			}
		}
	}
	
	&__content {
		background: $secondary;
		padding: 60px 110px;
		position: relative;
		.u-container {
			overflow: initial;
		}
		&:after {
			content: '';
			display: block;
			width: 160px;
			height: 100%;
			position: absolute;
			top: 0;
			right: 0;
			background: $primary;
		}
	}
	&__wheel {
		@include transforms(translateY(-50%), center);
		@include calc(height, 100%, '+', 100px);
		width: 225px;
		object-fit: contain;
		object-position: center left;
		position: absolute;
		left: 0;
		top: 50%;
		z-index: 3;
