		top: 0;
		.spacer {
			display: block;
			width: 1px;
			height: 150px;
			background: $white;
			margin: -40px auto 0 auto;
		}
		a {
			display: block;
			width: 20px;
			height: 20px;
			margin: 10px auto 0 auto;
