}
.s-listPosts {
	padding: 40px 0 80px 0;
	&__header {
		margin-bottom: 20px;
		h1 {
			display: block;
			width: 100%;
			text-transform: uppercase;
			font-size: 60px;
			color: $white;
		}
		p {
			display: block;
			width: 100%;
			text-transform: uppercase;
			font-size: 22px;
			color: $secondary;
			a {
				font-size: 22px;
				color: $white;
				text-decoration: underline;
				&:hover {
					text-decoration: none;
				}
			}
		}
	}
	&__pagination {
		margin-top: 20px;
		.c-pagination {
			margin: 0 auto;
			width: 100%;
			max-width: 440px;
		}
	}
	.c-responsiveGrid {
		@include calcWidthForPositionNegative(-20px);
		li {
			padding: 20px;
		}
	}
}

.s-defaultInnerSection {
	&__header {
		display: block;
		width: 100%;
		max-width: 790px;
		margin-bottom: 40px;
		h1 {
			font-weight: $fwRegular;
			font-size: 42.5447px;
			line-height: 44px;
			text-transform: uppercase;
			letter-spacing: -0.05em;
			color: $secondary;
			margin-bottom: 10px;
		}
		p {
			font-size: 25px;
			color: $white;
			font-weight: $fwRegular;
			line-height: 26px;
			a {
				color: $white;
				text-decoration: underline;
				&:hover {
					text-decoration: none;
					color: $secondary;
				}
			}
		}
	}
	&__content {
		article {
			p {
				@include paragrafos($tc: $white, $fs: 20px, $mbLOT: 20px);
				font-family: $fontFamily__default;
				text-align: justify;
				text-justify: inter-word;
			}
			.s-contentDefault h1, > h1 {
				text-transform: uppercase;
				color: $white;
				font-size: 42px;
				line-height: 1;
				margin: 40px 0 40px 0;
			}
			h2 {
				text-transform: uppercase;
				color: $white;
				font-size: 40px;
				line-height: 1;
				margin: 80px 0 40px 0;
			}
			figure {
				margin-bottom: 20px;
			}
			figure, img {
				display: block;
				width: 100%;
			}
			
			ol, ul {
				margin-bottom: 40px;
				li {
					display: list-item;
					list-style-position: inside;
					@include paragrafos($mb: 10px, $fs: 18px, $tc: $white);
					font-family: $fontFamily__default;
					color: $secondary;
					p {
						@include paragrafos($tc: $white, $fs: 20px, $mbLOT: 20px);
						font-family: $fontFamily__default;
						text-align: justify;
					}
					> * {
						display: inline;
					}
				}
			}
			ol {
				li {
					list-style-type: decimal-leading-zero;
				}
			}
			ul {
				li {
					list-style-type: disc;
				}
			}
			h3, h4, h5, h6 {
				text-transform: uppercase;
				color: $white;
				margin-bottom: 10px;
				margin-top: 30px;
			}
			h3 {
				font-size: 30px;
			}
			h4 {
				font-size: 26px;
			}
			h5 {
				font-size: 24px;
			}
			h6 {
				font-size: 22px;
			}
		}
	}
}
.s-gridPost {
	.s-defaultInnerSection__content {
		> ul {
			li {
				position: relative;
				padding-bottom: 40px;
				margin-bottom: 40px;
				&:before, &:after {
					content: '';
					display: block;
					position: absolute;
				}
				&:before {
					width: 100%;
					height: 1px;
					background: #FFFFFF;
					opacity: 0.2;
					bottom: 0;
					left: 0;
				}
				&:after {
					background: $secondary;
					width: 60px;
					height: 3px;
					left: 50%;
					bottom: -1px;
					margin-left: -30px;
				}
			}
		}
	}
}
.s-partners {
	&__list {
		width: 100%;
		margin: 0 auto;
		max-width: 1100px;
		display: flex;
		flex-direction: row;
		align-items: center;
		justify-content: center;
		flex-wrap: wrap;
		li {
			min-width: 25%;
			max-width: 50%;
			flex: 1 1 auto;
			padding: 30px;
			list-style-type: none;
			img {
				display: block;
				margin: 0 auto;
				width: 100%;
				max-width: 200px;
			}
		}
