			p {
				font-size: 14px;
				span {
					color: $secondary;
				}
			}
			h3 {
				font-size: 18px;
				text-transform: none;
				margin-bottom: 20px;
				strong, a {
					color: $secondary
				}
				a {
					&:hover {
						text-decoration: underline;
					}
				}
			}
			ol, li {
				font-size: 10px;
				p {
					font-size: 14px;
				}
			}
		}
	}
	
	&__titleForm {
		display: flex;
		flex-direction: row;
		align-items: center;
		justify-content: space-between;
		padding: 0 0 5px 20px;
		h4 {
			font-size: 16px;
			color: #FFC700;
			font-weight: $fwMedium;
		}
		span {
			font-size: 14px;
