// s-boilerplate__
.s-boilerplate__components {
	.componentsList {
		padding: 80px 40px;
		li {
			padding: 5px;
		}
		a {
			@include transition();
			@include roundedAll();
			display: block;
			width: 100%;
			text-align: center;
			padding: 30px;
			background: $mutedGray;
			color: $text;
			font-size: 16px;
			&:hover {
				background: $lightGray;
			}
		}
	}
}
.s-boilerplate__page {
	padding: 40px 0;
	&__title {
		@include flexWidth(100%);
		display: block;
		font-size: 44px;
		font-weight: 300;
		color: $secondary;
		text-align: center;
		margin-bottom: 40px;
	}
	&__listCpts {
		display: flex;
		flex-direction: row;
		justify-content: center;
		flex-wrap: wrap;
		width: 100%;
		border-bottom: 1px solid $mediumGray;
		padding-bottom: 60px;
		margin-bottom: 60px;
		&:last-child {
			border-bottom: 0;
			padding-bottom: 0;
			margin-bottom: 0;
		}
	}
	.component {
		@include roundedAll(10px);
		@include flexWidth(30%);
		display: flex;
		flex-direction: column;
		flex-wrap: nowrap;
		margin: 10px;
		background: teal;
		.component__header {
			padding: 15px;
			> h2 {
				color: $white;
				font-size: 18px;
			}
		}
		.component__content {
			@include roundedAll(10px);
			display: block;
			padding: 15px;
			background: $white;
			border: 3px solid teal;
			flex: 1 1 auto;
		}
		.content__hero {
			margin-bottom: 15px;
			padding-bottom: 15px;
			border-bottom: 1px solid $mutedGray;
			&:last-of-type {
				margin-bottom: 0;
				padding-bottom: 0;
				border-bottom: 0;
			}
			> h3 {
				color: $baseGray;
				text-transform: uppercase;
				font-weight: bold;
				font-size: 13px;
				margin-bottom: 4px;
			}
			.obs {
				font-size: 14px;
				color: $text;
				display: block;
				width: 100%;
				margin-top: 5px;
				padding-top: 5px;
				border-top: 1px solid $mutedGray;
				strong {
					font-weight: bold;
				}
				u {
					text-decoration: underline;
				}
				i {
					font-style: italic;
				}
			}
		}
	}
}

