// cardMkp
@media screen and (max-width: 640px) {
	.c-cardMkp {
		width: 350px;
		height: 202px;
		&__vip {
			right: 20px;
			top: 18px;
			line-height: 1;
			font-size: 20px;
		}
		&__brand {
			top: 20px;
			padding-left: 20px;
			img {
				max-width: 130px;
				margin: 0;
			}
		}
		&__infos {
			padding-left: 20px;
			padding-bottom: 20px;
			.ownerName {
				font-size: 20px;
			}
			.ownerDate {
				font-size: 14px;
				text-transform: uppercase;
			}
			.ownerNumber {
				font-size: 24px;
			}
		}
		&__qr {
			right: 20px;
			bottom: 20px;
			width: 50px;
			height: 50px;
		}
	}
}
@media screen and (max-width: 400px) {
	.c-cardMkp {
		@include calcWidthForPositionNegative(-10px);
		//width: 300px;
		height: 173px;
		&__brand {
			img {
				max-width: 110px;
			}
		}
		&__infos {
			.ownerName {
				font-size: 18px;
			}
			.ownerDate {
				font-size: 13px;
			}
			.ownerNumber {
				font-size: 20px;
			}
		}
		&__qr {
			right: 20px;
			bottom: 20px;
			width: 40px;
			height: 40px;
		}
	}
}

// c-cardQR
@media screen and (max-width: 640px) {
	.c-cardQR__qr img {
		width: 120px;
	}
	.c-cardQR__owner span {
		font-size: 28px;
	}
}